import React , {Fragment ,useState} from 'react'
import { Link } from 'gatsby'
import { navigate } from '@reach/router'
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import { makeStyles } from '@material-ui/core/styles'
import AppCard from '../sections/Card'
import actions from '../../store/actions'
import { useDispatch, useSelector} from 'react-redux'
import CustomButton from '../sections/Button'
import Loader from '../sections/Loader'
import AppMenu from '../MenuDropDown'
import './home.scss'

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1
  },
  navigation: {
    margin: "2% auto 0"
  },
  buttonWrapper: {
    textAlign: "right"
  }
}));

/* Home component - Holds App list */
const Home = () => {
  const classes = useStyles();
  const [statusFlag, setStatusFlag] = useState();

  const data = useSelector(state => state.notification);
  const navItems = data.allApplications.length > 0 ? data.allApplications : [];
  const arrayCheck = Array.isArray(navItems);
  const dispatch = useDispatch();
  const statusChanged = !!data ? data.statusChanged : "";
  React.useEffect(() => {
    if (!!data ? !!data.allApplications.length <= 0 : 0) {
      dispatch(actions.getAllApplication());
    }
    dispatch(actions.clearTemplateDetail());

  }, [dispatch]);
  React.useEffect(() => {

    setStatusFlag(statusChanged);
  }, [statusChanged]);
  const appSelection = value => {
    //test and uncomment below line - ----
    //    dispatch(actions.getSelectedApp(value.appId));
    dispatch(actions.setSelectedAppStatus(false))
    dispatch(actions.setSelectedApp(value));
    //comment below 2 lines -  List will load from notification tab/template tab
    dispatch(actions.getChannelList(value.appId));
    dispatch(actions.getCategoryList(value.appId));
  };
  //   const customClass = "appMenu";

  return (
    <Fragment>
      {/* {statusChanged && <Toast/>} */}
      {navItems && arrayCheck && navItems.length ? (
        <nav className={classes.navigation}>
          <Grid container justify="space-evenly" spacing={6}>
            <Grid xs={12} item className={classes.buttonWrapper}>
              <CustomButton
                type="primary"
                text="Onboard Application"
                action={() => {
                  dispatch(actions.setAppEdit(false));
                  navigate("/app/onboardingform");
                }}
              />
            </Grid>
            {data &&
              navItems &&
              arrayCheck &&
              navItems.length &&
              navItems.map((value, i) => (
                <Grid xs={12} md={4} key={i} item className="cardItems">
                  {/* <IconButton className="icon"  aria-label="settings">
                            <MoreVertIcon />
                        </IconButton> */}
                  {/* <AppMenu appId ={`menu-${value.appId}`}/> */}
                  <AppMenu
                    appId={value.appId}
                    status={value.active === true ? "Active" : "Inactive"}
                    name={value.appName}
                    image={value.appImageData}
                    description={value.appDescription}
                  />

                  <Link
                    to={`/app/id:${value.appId}/notifications`}
                    onClick={() => {
                      appSelection(value);
                    }}
                  >
                    <Paper>
                      <AppCard cardInfo={value}></AppCard>
                    </Paper>
                  </Link>
                </Grid>
              ))}
          </Grid>
        </nav>
      ) : (
        <Loader />
      )}
    </Fragment>
  );
};

export default Home;
