import React,{ useEffect,useState }  from 'react';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import PersonIcon from '@material-ui/icons/Person';
import IconButton from '@material-ui/core/IconButton';
import actions from '../../store/actions';
import { useDispatch, useSelector} from 'react-redux';
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import SearchIcon from '@material-ui/icons/Search';
import CloseIcon from '@material-ui/icons/Close';
import ProfileImage from '../sections/ProfileImage';
import Loader from '../sections/Loader';
import './userList.scss';

/* UserList component - Recipient List in Manage Template page */
export default function SwipeableTemporaryDrawer({id}) {
  const [state, setState] = React.useState({
    left: false,
    right: false,
  });
  const [search, setSearch] = useState("");
  var [recipientList, setRecipientList] = useState([]);
  var [updatedList, setUpdatedList] = useState([]);
  const data = useSelector(state => state.notification);
  const dispatch = useDispatch();
  // Regular expression to validate Email
  const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  
  useEffect(()=> {
    const list = !!data ? ( !!data.usersList ? data.usersList.filter((user => {
        return re.test(String(user.email).toLowerCase())
      })) : null ) : null ;
    setRecipientList(list);
    setUpdatedList(list);
  },[data]);

  const toggleDrawer = (side, open) => event => {
      if(open){
        dispatch(actions.getUserList(id));
      } else {
        dispatch(actions.setUserList([])); 
      }
      if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
        return;
      }
      setState({ ...state, [side]: open });
  };
    // Filter Recipient List as per user entry
  const filterList = (e) => {  
    console.log("recipient list...") ;
    const searchTerm = e.target.value;
    setSearch(searchTerm);
    let list = [];
    list = recipientList.filter(user => {
        //return(user.email.includes(searchTerm));
        return(user.email.startsWith(searchTerm));
    }) ; 
    setUpdatedList([]);
    (searchTerm) ? setUpdatedList(list) : setUpdatedList(recipientList) ;
  }

  const sideList = side => (
    <div
      className="recipientList"
      role="presentation"
    >
    {
        (data && data.usersList && updatedList.length > 0) ? 
        <List>
        <ListItem key="searchRecipients"> 
            <TextField
                className ="searchTxt"
                variant="outlined"
                size="small"
                placeholder="Search Name"
                value={search}
                onChange={filterList}
                InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <SearchIcon />
                      </InputAdornment>
                    ),
                  }}
            />
            <CloseIcon className="close" onClick={toggleDrawer('right', false)}/>
        </ListItem>
        { (data && data.usersList && updatedList.length > 0) &&
          updatedList.map((value, index) => (
          <ListItem  key={index}>
            <ListItemAvatar
                edge="end"
                aria-label="account of current user"
                color="inherit"
            >
            <ProfileImage profileID = {value.profileId} />
            </ListItemAvatar>
            <ListItemText className="listTxt" primary={value.email} />
          </ListItem>
        ))}
      </List>
        : <Loader/>
    }
    </div>
  );

  return (
    <div>
        <IconButton
            edge="start"
            color="inherit"
            aria-label="open drawer"
            onClick={toggleDrawer('right', true)}>
            <PersonIcon />
        </IconButton> 
        <Drawer anchor="right" open={state.right} >
        {sideList('right')}
        </Drawer> 
    </div>
  );
}