import React, { useState , Fragment} from "react";
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import Loader from "../sections/Loader"
// import Toast from '../Toast'
import './table.scss';

/* Table component */
const StickyHeadTable = ({
  type,
  tableData,
  start,
  setStart,
  columns,
  createData,
  pageSize,
  setPageSize,
  totalCount,
  page,
  setPage
}) => {
  
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [rowsData, setRowsData] = useState([]);
  const [count, setCount] = useState(0)

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    setStart((newPage+1)); 
    window.scrollTo({ top: "0", behavior: "smooth" });

  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(+event.target.value);
     setPage(0);
     setStart(0)
    setPageSize(event.target.value)
  };
  const clickedRow = row => {
    // alert(JSON.stringify(row.name));
  };
  let rows = [];

  React.useEffect(() => {
      setCount(totalCount)
    if (!!tableData) {
      switch(type) {
        case "notification" : 
          {
            tableData.map((value, index) => {
              rows.push(
                createData(
                  value.notificationName,
                  value.categoryname,
                  value.updatedDate,
                  value.status,
                  value.notificationId,
                  value.categoryId,
                  value.description
                )
              ); return null;
            });
            break;
          }
          case "template" :
              {
                tableData.map((value, index) => {
                  rows.push(
                    createData(
                      value.notificationName,
                      value.channelName,
                      value.templateName,
                      value.updateDate,
                      value.status,
                      value.templateId,
                      value.notificationId,
                      value.channelId
                    )
                  );return null;
                });
                break;
              }
              case "reports":
                  {
                    tableData.map((value, index) => {
                        rows.push(
                          createData(
                            value.notifcationName,
                            value.templateName,
                            value.itemId,
                            value.channelName,
                            value.sentDate,
                            value.count
                          )
                        );return null;
                        
                      });
                      break
                }
        default : return null;
      }
    }
    setRowsData(rows);
  }, [tableData,start,columns,page,type,totalCount]);

  return (
    <Fragment>
      {/* {statusChanged && <Toast/>} */}
      <Paper className='root'>
        <TableContainer className='tableContainer'>
          <Table stickyHeader aria-label="sticky table" className='table'>
            <TableHead className='stickyHeader'>
              <TableRow className='tableRow'>
                {columns.map((column,index) => (
                  <TableCell
                    className='stickyHeader'
                    key={column.id}
                    align={column.align}
                    style={{ minWidth: column.minWidth }}
                  >
                    {column.label}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            {(rowsData.length > 0 )? <TableBody>
              {rowsData
                // .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row,index) => {
                  return (
                    <TableRow className='tableRow'
                      hover
                      role="checkbox"
                      tabIndex={-1}
                      key={row.code}
                      onClick={() => {
                        clickedRow(row);
                      }}
                    >
                      {columns.map((column,index) => {
                        const value = row[column.id];
                        return (
                          <TableCell
                            key={column.id}
                            align={column.align}
                            className='tableBody'
                          >
                            {column.format && typeof value === "number"
                              ? column.format(value)
                              : value}
                          </TableCell>
                        );
                      })}
                    </TableRow>
                  );
                })}
            </TableBody> :<TableBody><TableRow><TableCell height='230' align='center' colSpan={Object.keys(columns).length}>{ count === 0 ? <div>No data Found</div> :<Loader/> }</TableCell></TableRow></TableBody>}
          </Table>
        </TableContainer>
        {/* {!! totalCount ?  */}
        <TablePagination
          rowsPerPageOptions={[10, 20, 25]}
          component="div"
          className='paginaion'
          count={!!count ? count : 0}
          rowsPerPage={rowsPerPage}
          page={page}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        /> 
        {/* // : <div>No Data Found</div>} */}
      </Paper></Fragment>
  );
};
export default StickyHeadTable;
