import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import actions from "../../store/actions";
import FormHeader from "../FormHeader/index";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import InputAdornment from "@material-ui/core/InputAdornment";
import AddBoxRoundedIcon from "@material-ui/icons/AddBoxRounded";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import ControlPointSharpIcon from "@material-ui/icons/ControlPointSharp";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";
import CustomButton from "../sections/Button";
import FormTextBox from "../sections/FormTextBox";
import InlineFormTextBox from "../InlineFormTextBox/index";
import Label from "../sections/Label";
//import HighlightedTextarea from 'react-highlighted-textarea'
//import Editor from '../sections/ckedit'
import "./CreateTemplate.scss";

/* Create Template component */
const CreateTemplateForm = ({
  setShowCopy,
  tag,
  editInfo,
  channelList,
  notificationListData,
  appId,
  pageSize
}) => {
  // const info = {"tempID":tempID, "type":type, "channel":channel, "name":name, }
  const [newParameter, setNewParameter] = useState();
  const [parameterList, setParameterList] = useState([]);
  const [name, setName] = useState("");
  const [channel, setChannel] = useState("");
  const [type, setType] = useState("");
  const [content, setContent] = useState("");
  const [subject, setSubject] = useState("");
  const [sucessFlag, setSuccessFlag] = useState(false);
  const [loader, setLoader] = useState(false);
  const [pos, setPos] = useState("");
  const [apiFailureMsg, setApiFailureMsg] = useState("");
  const data = useSelector(state => state.notification);
  const templateData = !!data ? data.templateDetails : "";
  const templateDescription = !!templateData ? templateData.description : "";
  const templateContent = !!templateData ? templateData.content : "";
  const templateParametersString = !!templateData
    ? templateData.templateParameters
    : "";
  const templateParametersArray = templateParametersString
    ? JSON.parse(templateParametersString)
    : [];
  const postSuccess = !!data ? data.apiSuccessFlag : "";
  const setApiFailMsg = !!data ? data.apiFailureMsg : "";

  useEffect(() => {
    dispatch(
      actions.addBreadCrumb({
        label: `${tag === "edit" ? "Edit Template" : "Create Template"}`
      })
    );
  }, []);
  useEffect(() => {
    setName(editInfo.name);
    setType(editInfo.type);
    setChannel(editInfo.channel);
    setContent(templateContent);
    setSubject(templateDescription);
    setParameterList(templateParametersArray);
  }, [
    editInfo,
    channelList,
    // tag,
    templateContent,
    templateDescription,
    templateParametersString,
    dispatch
  ]);

  useEffect(() => {
    setSuccessFlag(postSuccess);
    // setLoader(true);
  }, [postSuccess]);

  useEffect(() => {
    setApiFailureMsg(setApiFailMsg);
    setLoader(false);
  }, [setApiFailMsg]);

  const getParameter = parameter => {
    setNewParameter(parameter);
  };
  const addParameterList = () => {
    const tempParameterList = [...parameterList];

    if (newParameter) {
      if (!parameterList.includes(newParameter)) {
        tempParameterList.push(newParameter.replace(" ", "").trim());
      }
    }
    setParameterList(tempParameterList);
    setNewParameter("");
  };
  const deleteParam = parameter => {
    const tempParameterList = [...parameterList];
    const c = content;
    if (c.includes(parameter)) {
      let e = c.split(`:${parameter} `).join("");
      setContent(e);
    }
    if (parameter) {
      tempParameterList.splice(tempParameterList.indexOf(parameter), 1);
      setParameterList(tempParameterList);
    }
  };
  const addParam = parameter => {
    const c = content;
    let d = c.substr(0, pos) + ":" + parameter + " " + c.substr(pos);
    setContent(d);
 
  
      }
    
  const changeSubject = event => {
    setSubject(event.target.value);
  };
  const changeContent = event => {
    setContent(event.target.value);
  };

  const changeName = event => {
    setName(event.target.value);
  };

  const currentPosition = event => {

    const posValue = !!event ? event.target.selectionStart:0;
    setPos(posValue);
  };
  const dispatch = useDispatch();

  const updateTemplate = () => {
    window.scrollTo({ top: "0", behavior: "smooth" });
    setLoader(true);
    dispatch(actions.setAPIFailMessage(""));

    let tempParam = parameterList.toString();
    let info;
    if (tag === "edit") {
      info = {
        name: name,
        description: subject,
        content: content,
        appId: appId,
        templateParameters: parameterList,
        templateId: editInfo.tempID
      };
    } else if (tag === "create") {
      info = {
        name: name,
        description: subject,
        content: content,
        appId: appId,
        channelId: channel,
        typeId: type,
        templateParameters: parameterList
      };
    }
    dispatch(actions.updateNotification({ info: info, tag: tag }));
  };
  const loadData = () => {
    setShowCopy(false);
    dispatch(
      actions.getAllNotTempData({
        info: {
          name: "",
          channelId: -1,
          startNum: 0,
          pageSize: pageSize,
          appId: appId
        },
        tab: "template"
      })
    );
  };

  return (
    <form className="form-container">
      <Grid container item xs={12}>
        <FormHeader
          title={tag === "edit" ? "Edit Template" : "Create Template"}
          icon={tag === "edit" ? "edit" : "file"}
          // setShowCopy={setShowCopy}
          setShowCopy={loadData}
          flag={sucessFlag}
          apiFailureMsg={apiFailureMsg}
        />
        <Grid
          container
          item
          xs={12}
          className={sucessFlag || loader ? "inactive-container" : ""}
        >
          <Grid container item xs={12} className={"channel-type-container"}>
            <Grid container item xs={12} md={8}>
              <Grid item xs={12} md={4}>
                <InlineFormTextBox
                  label={<Label label="Channel" />}
                  dropdown={tag === "edit" ? false : true}
                  listData={channelList}
                  selectedData={channel}
                  setData={setChannel}
                />
              </Grid>
              <Grid item xs={12} md={8}>
                <InlineFormTextBox
                  label={<Label label="Notification Type" />}
                  dropdown={tag === "edit" ? false : true}
                  listData={notificationListData}
                  selectedData={type}
                  setData={setType}
                />
              </Grid>
            </Grid>
          </Grid>
          <Grid
            container
            item
            xs={12}
            spacing="3"
            className={"template-name-container"}
          >
            <Grid item xs={12} md={6}>
              <FormTextBox
                label={<Label label="Template Name" />}
                helperTxt="0 out of 100 characters"
              />
              <TextField
                size="small"
                variant="outlined"
                className="input-field"
                value={name || ""}
                onChange={changeName}
                required
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <FormTextBox
                label={<Label label="Subject" />}
                helperTxt="0 out of 100 characters"
              />
              <TextField
                size="small"
                variant="outlined"
                className="input-field"
                value={subject || ""}
                onChange={changeSubject}
                required
              />
            </Grid>
          </Grid>
          <Grid container item xs={12} className={"content-container"}>
            <Grid item xs={12}>
              <FormTextBox label={<Label label="Content" />} />
            </Grid>
            <Grid item xs={12} md={8}>

                <TextField
                  id="check"
                  value={content || ""}
                  onChange={changeContent}
                  onClick={currentPosition}
                  size="small"
                  variant="outlined"
                  className="content-input-field "
                  multiline={true}
                  rows={30}
                  required
                /> 

                {/* <Editor content = {content} setContent = {setContent} currentPosition = {currentPosition} setPos = {setPos}/> */}

            </Grid>
            <Grid item xs={12} md={4}>
              <div className="parameter-container">
                <div className="parameter-input-container">
                  <div className="parameter-content">List of Parameters</div>
                  <TextField
                    size="small"
                    variant="outlined"
                    className="parameter-input-field"
                    onChange={e => getParameter(e.target.value)}
                    value={newParameter || ""}
                    InputProps={{
                      endAdornment: (
                        <InputAdornment position="end">
                          <AddBoxRoundedIcon
                            fontSize="large"
                            htmlColor="#4FB218"
                            style={{ cursor: "pointer" }}
                            onClick={addParameterList}
                          />
                        </InputAdornment>
                      )
                    }}
                  />
                  <RadioGroup
                    aria-label="parameter"
                    name="parameter-lists"
                    className="radio-list-container"
                  >
                    {parameterList &&
                      parameterList.map(parameter => {
                        return (
                          <div className="parameter-radio-container">
                            <FormControlLabel
                              value={parameter}
                              className="parameter-radio-list"
                              control={<Radio size="small" />}
                              label={parameter}
                            />
                            <span className="icon-container">
                              <ControlPointSharpIcon
                                size="small"
                                onClick={() => addParam(parameter)}
                              />
                              <DeleteOutlineIcon
                                size="small"
                                onClick={() => deleteParam(parameter)}
                              />
                            </span>
                          </div>
                        );
                      })}
                  </RadioGroup>
                </div>
              </div>
            </Grid>
          </Grid>
          <Grid container className={"buttonWrapper"}>
            <CustomButton
              text="Cancel"
              type="secondary"
              action={() => {
                setShowCopy(false);
                dispatch(actions.setAPIFailMessage(""));
                dispatch(actions.toggleHeader(true));
                dispatch(actions.removeBreadCrumb());
              }}
            />
            <CustomButton
              text="Submit"
              type="primary"
              action={updateTemplate}
              disable={
                !!(!!name ? name.trim() : "") &&
                !!(
                  !!type &&
                  !!(!!subject ? subject.trim() : "") &&
                  !!channel &&
                  !!(!!content ? content.trim() : "")
                )
                  ? false
                  : true
              }
            />
          </Grid>
        </Grid>
      </Grid>
    </form>
  );
};

export default CreateTemplateForm;



