import React, { Fragment, useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import actions from "../../store/actions"
import StickyHeadTable from "../Table";
import SwitchesSize from "../sections/Switch";
import EditIcon from '@material-ui/icons/EditOutlined';
import Filter from '../Filter'
import CreateNotificationForm from "../CreateNotification";

/* Notifications component - Manage Notifications */
const Notification = ({appId}) => {
  const [filter, setFilter] = useState(-1);
  const [search, setSearch] = useState("");
  const [start, setStart] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [page, setPage] = React.useState(0);
  const [showEdit, setShowEdit] = useState(false);
  const [tag, setTag] = useState("");
  const [editInfo, setEditInfo] = useState({});
  const [channel, setChannel] = useState({});
  const [notifyData, setNotifyData] = useState([]);
  const [appIdURL, setAppIdURL] = useState();
  const [appStatus, setAppStatus] = useState();

  const data = useSelector(state => state.notification);
  const notificationData = !!data
    ? data.notData
      ? data.notData.records
      : ""
    : "";
  const totalCount = !!data
    ? data.notData
      ? data.notData.count
      : 0
    : 0;
    const selectedAppStatus = !!data
    ? data.selectedAppStatus
      :""
    // below code commented as we started picking the appId from url
    // const appId = !!data
    // ? data.selectedApp
    //   ? data.selectedApp.appId
    //   : 0
    // : 0;
    // const appStatus = !!data
    // ? data.selectedApp
    //   ? data.selectedApp.active
    //   : true
    // : true;

  const channelList = !!data
    ? data.channelList
      ? data.channelList.notificationChannels
      : ""
    : "";
  const categoryList = !!data
    ? data.categoryList
      ? data.categoryList.notificationCategories
      : ""
    : "";

  const columns = [
    { id: "type", label: "Notification Type", minWidth: 150 },
    {
      id: "category",
      label: "Category",
      minWidth: 150,
      align: "left",
      format: value => value.toLocaleString()
    },
    {
      id: "date",
      label: "Updated Date",
      minWidth: 150,
      align: "left"
    },

    {
      id: "statusData",
      label: "Status",
      minWidth: 20,
      align: "left"
    },

    {
      id: "edit",
      label: "",
      minWidth: 40,
      align: "center"
    }
  ];
  const editNotification = (
    category,
    type,
    description,
    categoryId,
    typeId
  ) => {
    dispatch(actions.toggleHeader(false));
    setShowEdit(true);
    setTag("edit");
    const info = {
      category: category,
      type: type,
      description: description,
      categoryId: categoryId,
      typeId: typeId
    };
    setEditInfo(info);
  };
  const createNotification = () => {
    dispatch(actions.toggleHeader(false));
    setShowEdit(true);
    setTag("create");
    setEditInfo({});
  };

  function createData(
    type,
    category,
    newDate,
    status,
    typeId,
    categoryId,
    description
  ) {
    const edit = (
      <EditIcon
        onClick={() => {
          editNotification(category, type, description, categoryId, typeId);
        }}
      />
    );
    const notificationId = typeId;
    const dateFormat = new Date(newDate);
    const date =
      dateFormat.getDate() +
      "/" +
      (dateFormat.getMonth() + 1) +
      "/" +
      dateFormat.getFullYear();

    const statusData = (
      <SwitchesSize
        status={status === 1 ? "Active" : "Inactive"}
        type="notification"
        typeId={notificationId}
        refreshData={refreshData}
      />
    );
    return { type, category, date, statusData, edit };
  }

  const dispatch = useDispatch();
  useEffect(() => {
    window.scrollTo({ top: "0", behavior: "smooth" });
  });
  const url = window.location.pathname;
  useEffect(() => {
    setAppStatus(selectedAppStatus)
  }, [selectedAppStatus]);
  useEffect(() => {
    setAppIdURL(url.split(":")[1].split("/")[0])
    dispatch(actions.toggleHeader(true));
    dispatch(actions.getChannelList(appId))

    const arrayCheck = Array.isArray(categoryList);
    if (!arrayCheck) {
      dispatch(actions.getCategoryList(appId));
    }
    dispatch(actions.getSelectedApp(appId));

  }, [appId]);

  useEffect(() => {
    setNotifyData(notificationData);
    setChannel(channelList);
  }, [notificationData,dispatch]);

  useEffect(() => {

    dispatch(
      actions.getAllNotTempData({
        info: {
          name: search,
          categoryId: filter,
          appId: appId,
          startNum: start,
          pageSize: pageSize
        },
        tab: "notification"
      })
    );
  }, [search, filter, start, page, pageSize, appId, dispatch]);

  const refreshData = () => {
    dispatch(
      actions.getAllNotTempData({
        info: {
          name: search,
          categoryId: filter,
          appId: appId,
          startNum: start,
          pageSize: pageSize
        },
        tab: "notification"
      })
    );
  };
  return (
    <form className="template">
      {!showEdit && (
        <Fragment>
          <Filter
            dropDownLabel="Category"
            category={categoryList}
            textBoxLabel="Notification Type"
            ButtonLabel="Create Notification"
            createAction={createNotification}
            filter={filter}
            setFilter={setFilter}
            search={search}
            setSearch={setSearch}
            start={start}
            setStart={setStart}
            setPage={setPage}
            appStatus={appStatus}
          />
          <StickyHeadTable
            type="notification"
            tableData={notifyData}
            start={start}
            setStart={setStart}
            columns={columns}
            createData={createData}
            pageSize={pageSize}
            setPageSize={setPageSize}
            totalCount={totalCount}
            page={page}
            setPage={setPage}
          />
        </Fragment>
      )}
      {showEdit && (
        <CreateNotificationForm
          setShowEdit={setShowEdit}
          tag={tag}
          editInfo={editInfo}
          appId={appId}
          categoryList={categoryList}
          pageSize={pageSize}
        />
      )}
    </form>
  );
};

export default Notification;
