import React, {Fragment ,useEffect }  from "react"
import { useDispatch ,useSelector } from "react-redux"
import actions from "../../store/actions"
import SimpleTabs from "../Tabs"
import BreadCrumbs from "../Breadcrumbs"
import Loader from "../sections/Loader"

/* Manage component - Entry Point to all manage pages - Manage Notifcations/Template/Reports  */
const Manage = ({appId}) => {
    console.log("*** manage loaded...........");
    // appId passed from Routers which is the appId in Browser url
    const data = useSelector(state => state.notification);
    //const appId = data ? data.selectedApp.appId : null;
    const appName = data ? data.selectedApp.appName : null;
    const dispatch = useDispatch();
    useEffect(() => {
        // dispatch(actions.getSelectedApp());
        const currentURL = window.location.href;
        if (currentURL.includes("notifications")){
            dispatch(actions.removeAllBreadCrumbs());
            dispatch(actions.addBreadCrumb({label :"Manage Notifications",path :`/app/id:${appId}/notifications`}));
        }
        else if(currentURL.includes("templates")){
            dispatch(actions.removeAllBreadCrumbs());
            dispatch(actions.addBreadCrumb({label :"Manage Templates",path :`/app/id:${appId}/templates`}));
        }
    },[dispatch, appId]);

    if ( appId ) {
        return (
            <Fragment>
                <BreadCrumbs appName ={ appName }/>
                <SimpleTabs appId = { appId }/>
            </Fragment>
        )

    } else {
        return (
            <Loader/>    
        )
    }  
    
}
export default Manage