import React, { Fragment, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import actions from "../../store/actions";
import { navigate } from "@reach/router";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import IconButton from "@material-ui/core/IconButton";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import SwitchesSize from "../sections/Switch";
import "./menuDropDown.scss";

/* Menu DropDown component - Home page App cards */
/* Entry point to App status change and App edit actions */
const AppMenu = ({ appId, status, image, name, description }) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const isMenuOpen = Boolean(anchorEl);

  const dispatch = useDispatch();
  const data = useSelector(state => state.notification);
  const allApp = data.allApplications.length > 0 ? data.allApplications : [];
  const handleAppMenuOpen = event => {
    setAnchorEl(event.currentTarget);
  };
  const handleMenuClose = () => {
    setAnchorEl(null);
  };
  const editApp = () => {
    dispatch(actions.setAppEdit(true));
    dispatch(
      actions.setAppEditInfo({
        appId: appId,
        image: image,
        name: name,
        description: description
      })
    );
    navigate("/app/onboardingform");
  };
  const refreshData = val => {
    let c = allApp;
    let d ;
    if (c) {
      c.map((value, index) => {
        if (value.appId == val) {
        d = c[index]
        
        console.log(d.active)
        let s = (d.active ===true) ? false : true

        d.active = s
        console.log(s)
        console.log(d)

       c.splice(index, 1);
       }
      });
      c.push(d)

     // dispatch(actions.setAllApplication([]));
      dispatch(actions.setAllApplication(c));
    }
  };

  const renderMenu = (
    <Menu
      className="menuDropDown"
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
      id={appId}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      {/* <MenuItem onClick={handleMenuClose}> */}
      <MenuItem>
        <SwitchesSize
          status={status}
          type="application"
          typeId={appId}
          labelPlacement="start"
          refreshData={refreshData}
        />
      </MenuItem>
      <MenuItem className="edit" onClick={editApp}>
        Edit
      </MenuItem>
    </Menu>
  );

  return (
    <div className="menuDropDown">
      <IconButton
        className="icon"
        aria-label="settings"
        onClick={handleAppMenuOpen}
      >
        <MoreVertIcon />
      </IconButton>
      {renderMenu}
    </div>
  );
};

export default AppMenu;
