import React, { useEffect, useState , Fragment} from "react";
import { useDispatch , useSelector} from "react-redux";
import actions  from "../../store/actions";
import FormHeader from "../FormHeader/index";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import CustomButton from "../sections/Button";
import "./payload.scss";

/* CopyPayload component */
const CopyPayload = ({ tempID, setShowCopy ,notID}) => {  
  const dispatch = useDispatch();
  const [samplePayload, setSamplePayload] = useState("");
  const [sucessFlag, setSuccessFlag] = useState("");
  const [loader, setLoader] = useState(false);

  const data = useSelector(state => state.notification);

  const payLoadData = JSON.stringify(!!data ? data.samplePayload : "", null,4 );
  const postSuccess = !!data ? data.apiSuccessFlag : "";

  useEffect(() => {
    setSamplePayload(payLoadData)
  }, [payLoadData]);

  useEffect(() => {
    setSuccessFlag(postSuccess);
    setLoader(false)
  }, [postSuccess]);
  useEffect(() => {
    dispatch(
      actions.addBreadCrumb({
        label: `Payload`
      })
    );
  }, []);
  const changePayload =(event) =>{
    setSamplePayload(event.target.value)
    }
  const testPayload = () =>{
    setLoader(true)
    
    let formattedData
    try{
      formattedData = JSON.parse(samplePayload)
      formattedData.notificationTypeList[0].notificationTypeId  = notID;
       dispatch(actions.testSamplePayload(formattedData));
    }
    catch(error){
      setLoader(false)
      alert("input value in correct format")

    }
   


// 
//     dispatch(actions.testSamplePayload({
//       notificationTypeId:201,
//       docId:"75849309asd567483",
//       toIds:[" teamPepsiCo@bubly.com"],
//       ccIds:["teamPepsiCo@bubly.com"],
//       bccIds:["teamPepsiCo@bubly.com"],
//       inputs:{"ownerName":"Team","contentURL":"http://mypepsico.com"}
// }));
  }
  return (
    <Fragment>
    <div className="form-container payload-container">
      <FormHeader title="Pay Load" icon="file" setShowCopy={setShowCopy} flag = {sucessFlag}
/>
      <div className = {(sucessFlag || loader) ? "inactive-container":""}>
      <div className="payload-title">Sample Payload</div>
      {/* <Grid container item xs={10} className = "payload-sample"> */}
        {/* <pre>{payLoadData}</pre> */}
        <TextField  variant="outlined" className="payloadTextArea" multiline={true} rows={20} value= {samplePayload} onChange =  {changePayload}>
       </TextField>
      {/* </Grid> */}
      <Grid item xs={10} className="payload-button">
        <div className="create-template-action-container">
          <CustomButton text="Cancel" type="secondary" action = {() => {
            setShowCopy(false)
            dispatch(actions.toggleHeader(true));
            }}/>
          <CustomButton text="Test" type="primary" action = {() =>{testPayload()}} />
        </div>
      </Grid>
      </div>
    </div>
    </Fragment>
  );
};

export default CopyPayload;
