import React , { Fragment ,useState } from "react"
import { Router } from "@reach/router"
import { useDispatch ,useSelector } from "react-redux"
import Home from "../Home"
import Manage from "../Manage"
import OnBoradingForm from "../OnBoardForm"
import actions from "../../store/actions"
/* Router component */
const AppRouter = ({props}) => {
    const data = useSelector(state => state.notification);
    const [appIdURL, setAppIdURL] = useState(null);
    let [finalAppId ,setFinalAppId] = useState(null);
    // picks the current browser url
    //const url = window.location.pathname;
    const url = null;
    //appId = data ? data.selectedApp.appId : null;
    // appId present in url then use that otherwise appId in redux store
    const appId = (!!data && data.selectedApp) ? ( data.selectedApp.appId ? data.selectedApp.appId : appIdURL) : appIdURL;
    const dispatch = useDispatch();
    React.useEffect(() => {
        const url = window.location.pathname;
        console.log("url..")
        console.log(url)
        // exracts appId from url
        if(url && url.includes('id:')) {
            setAppIdURL(url.split(":")[1].split("/")[0]);  
        } else {
            setAppIdURL(null);
        }
    },[]);
    React.useEffect(() => {
        appIdURL ? setFinalAppId(appIdURL) :  setFinalAppId(appId);
        //setFinalAppId(appIdURL);
        //dispatch(actions.getSelectedApp());
    },[appIdURL,appId]);
    
    React.useEffect(() => {
        if(finalAppId){
            dispatch(actions.getSelectedApp(finalAppId));
        }
    },[dispatch,finalAppId]);



    return (
        <Fragment>
            {/* <div>Rouetr
            {`/app/id:${finalAppId}`}
            </div> */}
            <Router>
                <Home path="/app"/>
                <Manage path={`/app/id:${finalAppId}/*`} appId ={`${finalAppId}`}/>
                <OnBoradingForm path="/app/onboardingform"/>
                {/* <Manage path={`/app/id:${appId}/*`} >
                    <Display path={`/app/id:${appId}/templates`} label="Manage Templates" />
                    <Display path={`/app/id:${appId}/reports`} label="Manage Reports" />
                    <Display path={`/app/id:${appId}/notifications`} label="Manage Notifications" />
                </Manage> */}
            </Router>
        </Fragment>
    )
}
export default AppRouter