import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import actions from "../../store/actions";
import FormHeader from "../FormHeader/index";
import Grid from "@material-ui/core/Grid";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import TextField from "@material-ui/core/TextField";
import CustomizedButton from "../sections/Button";
import FormTextBox from "../sections/FormTextBox";
import Label from "../sections/Label";
import "./CreateNotification.scss";

/* Create Notification Form component */
const CreateNotificationForm = ({
  setShowEdit,
  tag,
  editInfo,
  appId,
  categoryList,
  pageSize

}) => {
  //const [edit, setEdit] = useState("");
  const [dropdown, setDropdown] = useState("");
  const [description, setDescription] = useState("");
  const [ntType, setNtType] = useState("");
  const [typeId, setTypeId] = useState("");
  const [sucessFlag, setSuccessFlag] = useState("");
  const [loader, setLoader] = useState(false);
  const [apiFailureMsg, setApiFailureMsg] = useState("");

  const dispatch = useDispatch();
  const data = useSelector(state => state.notification);
  const postSuccess = !!data ? data.apiSuccessFlag : "";
  const setApiFailMsg = !!data ? data.apiFailureMsg : "";

  useEffect(() => {
    dispatch(
      actions.addBreadCrumb({
        label: `${tag === "edit" ? "Edit Notification" : "Create Notification"}`
      })
    );
  }, []);
  useEffect(() => {
    //setEdit(editInfo);
    setDropdown(editInfo.categoryId);
    setDescription(editInfo.description);
    setNtType(editInfo.type);
    setTypeId(editInfo.typeId);
    // dispatch(
    //   actions.addBreadCrumb({
    //     label: `${tag === "edit" ? "Edit Notification" : "Create Notification"}`
    //   })
    // );
  }, [editInfo, dispatch, tag]);

  useEffect(() => {
    setSuccessFlag(postSuccess);
    // setLoader(false)
  }, [postSuccess]);

  useEffect(() => {
    // setSuccessFlag(false);
    setApiFailureMsg(setApiFailMsg);
    setLoader(false);
  }, [setApiFailMsg]);
  const changeID = event => {
    setDropdown(event.target.value);
  };
  const changeType = event => {
    setNtType(event.target.value);
  };
  const changeDescription = event => {
    setDescription(event.target.value);
  };

  const createNotification = () => {
    window.scrollTo({ top: "0", behavior: "smooth" });
    setLoader(true);
    dispatch(actions.setAPIFailMessage(""));
    dispatch(
      actions.createNewNotification({
        name: ntType,
        description: description,
        categoryId: dropdown,
        appId: appId
      })
    );

  };

  const  loadData = () =>{
    setShowEdit(false)
    dispatch(
      actions.getAllNotTempData({"info":{
        name: "",
        categoryId: -1,
        appId: appId,
        startNum: 0,
        pageSize: pageSize
      },"tab":"notification"})
    );
  }
  const editNotification = () => {
    window.scrollTo({ top: "0", behavior: "smooth" });
    setLoader(true);
    dispatch(actions.setAPIFailMessage(""));
    dispatch(
      actions.editNotification({
        name: ntType,
        description: description,
        categoryId: dropdown,
        typeId: typeId,
        appId: appId
      })
    );

  };
 

  return (
    <form className="create-notification-form-container">
      <Grid container item xs={12}>
        <FormHeader
          title={tag === "edit" ? "Edit Notification" : "Create Notification"}
          icon={tag === "edit" ? "edit" : "file"}
          // setShowCopy={setShowEdit}
          setShowCopy = {loadData}
          flag={sucessFlag}
          apiFailureMsg={apiFailureMsg}
        />
        <Grid
          container
          item
          xs={12}
          className={
            sucessFlag || loader
              ? "category-container inactive-container"
              : "category-container"
          }
        >
          <Grid item xs={4} md={1}>
            <div className="category-Label">
              <Label label="Category"></Label>
            </div>
          </Grid>
          <Grid item xs={8} md={2}>
            <FormControl
              size="small"
              variant="outlined"
              className="drop-down-wrapper"
              validate
            >
              <Select
                labelId="demo-simple-select-outlined-label"
                className="drop-down-content"
                value={dropdown || ""}
                onChange={changeID}
                required
              >
                {categoryList &&
                  categoryList.map((value, index) => {
                    return <MenuItem value={value.id}>{value.name}</MenuItem>;
                  })}
              </Select>
            </FormControl>
          </Grid>
        </Grid>
        <Grid
          container
          item
          xs={12}
          className={
            sucessFlag || loader
              ? "type-container inactive-container"
              : "type-container"
          }
        >
          <Grid item xs={12} md={6}>
            <FormTextBox
              label={<Label label="Notification Type" />}
              helperTxt="0 to 100 Characters"
            />
            <TextField
              required
              size="small"
              variant="outlined"
              className="type-input-field"
              value={ntType}
              onChange={changeType}
            />
          </Grid>
        </Grid>
        <Grid
          container
          item
          xs={12}
          className={
            sucessFlag || loader
              ? "inactive-container desc-container"
              : "desc-container"
          }
        >
          <Grid item xs={12}>
            <FormTextBox
              label={<Label label="Description" />}
              helperTxt="0 to 300 Characters"
              onSubmit={createNotification}
            />
            <TextField
              size="small"
              variant="outlined"
              className="desc-input-field"
              multiline={true}
              onChange={changeDescription}
              rows={9}
              value={description}
              required
            ></TextField>
          </Grid>
          <Grid container item xs={12}>
            <CustomizedButton
              text="Cancel"
              type="secondary"
              action={() => {
                dispatch(actions.setAPIFailMessage(""));
                setShowEdit(false);
                dispatch(actions.toggleHeader(true));
                dispatch(actions.removeBreadCrumb());
              }}
            />
            <CustomizedButton
              text="Submit"
              type="primary"
              action={tag === "create" ? createNotification : editNotification}
              disable={
                !!dropdown &&
                !!(!!description ? description.trim() : "") &&
                !!(!!ntType ? ntType.trim() : "")
                  ? false
                  : true
              }
            />
          </Grid>
        </Grid>
      </Grid>
    </form>
  );
};

export default CreateNotificationForm;
