import React from "react";
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles(theme => ({
      media: {
        height: 0,
        paddingTop: '30%', // 16:9
        margin: '0 20px'
      },
      header:{
          padding:'10px 10px 0 10px'
      }
}));

/* App Card Component */
const AppCard = ({cardInfo}) => {
    const classes = useStyles();
    return (
        <Card className="card">
            <CardMedia
                className={classes.media}
                image={`data:image/png;base64, ${cardInfo.appImageData}`}
                title="Paella dish"
            />
            <div className="title">
                <Typography variant="subtitle1" gutterBottom>{cardInfo.appName}</Typography>
                <Typography variant="subtitle2" gutterBottom>{cardInfo.appDescription}</Typography>
            </div>
        </Card>
    )
}

export default AppCard;
