import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import actions from "../../store/actions"
import  StickyHeadTable from "../Table"
import Filter from '../Filter';
import RecipientsList from '../UserList';

/* Report component - Manage Report */
const Report = ({appId}) => {
  
  const [filter, setFilter] = React.useState(101);
  const [search, setSearch] = React.useState("");
  const [start, setStart] = React.useState(0);
  const [page, setPage] = React.useState(0);
  const [pageSize, setPageSize] = useState(10);
  const [channel, setChannel] = useState({});
  const [dateCount, setDateCount] = useState(0);
  const [appIdURL, setAppIdURL] = useState();


  const data = useSelector(state => state.notification);
  const reportData = !!data
    ? data.allReport
      ? data.allReport.notifications
      : []
    : [];
    const totalCount = !!data
    ? data.allReport
      ? data.allReport.count
      : 0
    : 0;
    // below code commented as we started picking the appId from url
    // const appId = !!data
    // ? data.selectedApp
    //   ? data.selectedApp.appId
    //   : 0
    // : 0;
    const channelList = !!data
    ? data.channelList
      ? data.channelList.notificationChannels
      : "" :""
    const usersList = !!data
      ? data.usersList
        : "" 
  const columns = [
    { id: "type", label: "Notification Type", minWidth: 180 },
    {
      id: "name",
      label: "Template Name",
      minWidth: 120,
      align: "left",
      format: value => value.toLocaleString()
    },
    {
      id: "id",
      label: "Content ID",
      minWidth: 150,
      align: "left",
      format: value => value.toLocaleString()
    },

    {
      id: "channel",
      label: "Channel",
      minWidth: 20,
      align: "left"
    },

    {
      id: "recipients",
      label: "Recipients",
      minWidth: 30,
      align: "center"
    },
    {
      id: "date",
      label: "Notification Date",
      minWidth: 130,
      align: "left"
    },
    {
      id: "count",
      label: "No of Notifications",
      minWidth: 40,
      align: "center"
    }
  ];
  const dispatch = useDispatch();

  // const viewUserList = (data) =>{
  //   alert("display users list inprogress")
  //   dispatch(
  //     actions.getUserList(data)
  //   );
  // }
  function createData(type, name, id, channel, newDate, count) {
    const recipients = <RecipientsList id={id}/> 
    const dateFormat = new Date(1576130944351)
    const date = dateFormat.getDate() + '/' + (dateFormat.getMonth()+1) + '/' + dateFormat.getFullYear()
    return { type, name, id, channel, recipients, date, count };
    }

  useEffect(() => {
    setChannel(channelList)

    dispatch(
      actions.getAllReports({ 
        notificationName:search,
        channelId:filter,
        appId:appId,
        dateRange:dateCount,
        start: start,
        pageSize: pageSize
      })
    );
  }, [search, filter, start, page, pageSize, channelList,dateCount,appId, dispatch]);

  return (
    <div className="template">
    <Filter dropDownLabel ='Channel' channel = {channel} textBoxLabel = 'Notification Type' DateRangeLabel ='Date Range'  filter = {filter} setFilter = {setFilter} search = {search} setSearch ={setSearch} start = {start} setStart = {setStart} setPage ={setPage} setDateCount = {setDateCount}/>
    <StickyHeadTable
          type="reports"
          tableData={reportData}
          start={start}
          setStart={setStart}
          columns={columns}
          createData={createData}
          pageSize={pageSize}
          setPageSize={setPageSize}
          totalCount={totalCount}
          page={page}
          setPage={setPage}
        />
    </div> 
  );
};

export default Report;
