import React, { Fragment, useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import actions from "../../store/actions";
import StickyHeadTable from "../Table";
import SwitchesSize from "../sections/Switch";
import CopyPayload from "../CopyPayload";
import CreateTemplateForm from "../CreateTemplate";
import EditIcon from "@material-ui/icons/EditOutlined";
import ShowPayloadIcon from "@material-ui/icons/InsertDriveFile";
import Filter from "../Filter";

/* Template component - Manage Template */
const Template = ({ appId }) => {
  const [filter, setFilter] = useState(-1);
  const [search, setSearch] = useState("");
  const [start, setStart] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [page, setPage] = useState(0);
  const [showCopy, setShowCopy] = useState(false);
  const [showEdit, setShowEdit] = useState(false);
  const [tag, setTag] = useState("");
  const [channel, setChannel] = useState({});
  const [notificationListData, setNotificationListData] = useState({});
  const [editInfo, setEditInfo] = useState({});
  const [tempID, setTempID] = useState("");
  const [notID, setNotID] = useState("");
  const [updatedID, setUpdatedID] = useState("");
  const [payload, setPayload] = useState("");
  const [appStatus, setAppStatus] = useState();

  const columns = [
    { id: "type", label: "Notification Type", minWidth: 170 },
    {
      id: "channel",
      label: "Channel",
      minWidth: 150,
      align: "left",
      format: value => value.toLocaleString()
    },
    {
      id: "name",
      label: "Template Name",
      minWidth: 150,
      align: "left",
      format: value => value.toLocaleString()
    },
    {
      id: "date",
      label: "Updated Date",
      minWidth: 150,
      align: "left"
    },
    {
      id: "statusData",
      label: "Status",
      minWidth: 150,
      align: "left"
    },
    {
      id: "file",
      label: "",
      minWidth: 100,
      align: "right"
    },
    {
      id: "edit",
      label: "",
      minWidth: 100,
      align: "right"
    }
  ];
  const data = useSelector(state => state.notification);
  const templateData = !!data
    ? data.tempData
      ? data.tempData.records
      : ""
    : "";
  const totalCount = !!data ? (data.tempData ? data.tempData.count : 0) : 0;
  // below code commented as we started picking the appId from url
  //const appId = !!data ? (data.selectedApp ? data.selectedApp.appId : "") : "";
  // const appStatus = !!data
  //   ? data.selectedApp
  //     ? data.selectedApp.active
  //     : true
  //   : true;
  const channelList = !!data
    ? data.channelList
      ? data.channelList.notificationChannels
      : ""
    : "";
  const notificationList = !!data
    ? data.notificationList
      ? data.notificationList.notificationTypes
      : ""
    : "";
  const updatedTemplate = !!data ? data.updatedTemplate : "";

  const selectedAppStatus = !!data ? data.selectedAppStatus : "";
  const dispatch = useDispatch();

  const showPayload = (tempID, notID) => {
    setTempID(tempID);
    setNotID(notID);
    dispatch(actions.toggleHeader(false));
    dispatch(actions.getSamplePayload(tempID));
    setShowCopy(true);
  };
  const editTemplate = (tempID, type, channel, name, notID, channelID) => {
    dispatch(actions.toggleHeader(false));
    dispatch(actions.getNotificationList(appId));

    setShowEdit(true);
    setTag("edit");
    const info = {
      tempID: tempID,
      type: type,
      channel: channel,
      name: name,
      notID: notID,
      channelId: channelID
    };
    setEditInfo(info);
    dispatch(actions.getTemplateDetail({ tempID: tempID, appId: appId }));
  };
  const createTemplate = () => {
    dispatch(actions.toggleHeader(false));
    dispatch(actions.getNotificationList(appId));

    setShowEdit(true);
    setTag("create");
    setEditInfo({});
    // dispatch(actions.clearTemplateDetail());
  };

  const createData = (
    type,
    channel,
    name,
    newDate,
    status,
    tempID,
    notID,
    channelID
  ) => {
    const edit = (
      <EditIcon
        onClick={() => {
          editTemplate(tempID, type, channel, name, notID, channelID);
        }}
      />
    );
    const file = (
      <ShowPayloadIcon
        style={{ color: !status ? "gray" : "" }}
        onClick={
          status
            ? () => {
                showPayload(tempID, notID);
              }
            : null
        }
      />
    );
    const dateFormat = new Date(newDate);
    const date =
      dateFormat.getDate() +
      "/" +
      (dateFormat.getMonth() + 1) +
      "/" +
      dateFormat.getFullYear();
    const statusData = (
      <SwitchesSize
        status={status === 1 ? "Active" : "Inactive"}
        type="template"
        typeId={tempID}
        refreshData={refreshData}
      />
    );
    return { type, channel, name, date, statusData, file, edit };
  };
  useEffect(() => {
    dispatch(actions.toggleHeader(true));
  }, []);
  useEffect(() => {
    setNotificationListData(notificationList);
    setUpdatedID(updatedTemplate);
  }, [notificationList, updatedTemplate]);
  useEffect(() => {
    window.scrollTo({ top: "0", behavior: "smooth" });
  });
  useEffect(() => {
    setAppStatus(selectedAppStatus);
  }, [selectedAppStatus]);
  useEffect(() => {
    setChannel(channelList);
    dispatch(
      actions.getAllNotTempData({
        info: {
          name: search,
          channelId: filter,
          startNum: start,
          pageSize: pageSize,
          appId: appId
        },
        tab: "template"
      })
    );
  }, [search, filter, start, pageSize, page, channelList, appId, dispatch]);
  const refreshData = () => {
    dispatch(
      actions.getAllNotTempData({
        info: {
          name: search,
          channelId: filter,
          startNum: start,
          pageSize: pageSize,
          appId: appId
        },
        tab: "template"
      })
    );
  };
  return (
    <div className="template">
      {!showCopy && !showEdit && (
        <Fragment>
          <Filter
            dropDownLabel="Channel"
            channel={channel}
            textBoxLabel="Template Name"
            ButtonLabel="Create Template"
            createAction={createTemplate}
            filter={filter}
            setFilter={setFilter}
            search={search}
            setSearch={setSearch}
            start={start}
            setStart={setStart}
            setPage={setPage}
            appStatus={appStatus}
          />
          <div className="table">
            <StickyHeadTable
              type="template"
              tableData={templateData}
              start={start}
              setStart={setStart}
              columns={columns}
              createData={createData}
              pageSize={pageSize}
              setPageSize={setPageSize}
              totalCount={totalCount}
              page={page}
              setPage={setPage}
            />
          </div>
        </Fragment>
      )}
      {showCopy && (
        <CopyPayload tempID={tempID} setShowCopy={setShowCopy} notID={notID} />
      )}
      {showEdit && (
        <CreateTemplateForm
          setShowCopy={setShowEdit}
          tag={tag}
          editInfo={editInfo}
          channelList={channelList}
          notificationListData={notificationListData}
          appId={appId}
          pageSize={pageSize}
        />
      )}
    </div>
  );
};

export default Template;
