import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import actions from "../../store/actions";
import Switch from "@material-ui/core/Switch";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";

/* Custom Switch Component */
/* Change status active/Inactive */
const SwitchesSize = ({
  status,
  type,
  typeId,
  labelPlacement,
  refreshData
}) => {
  const labelPosition = labelPlacement == "start" ? "start" : "end";
  const data = useSelector(state => state.notification);
  const appId = !!data ? (data.selectedApp ? data.selectedApp.appId : "") : "";
  const statusChanged = !!data ? data.statusChanged : ""

  // const statusChanged = !!data ? data.statusChanged   : "";
  const [checked, setChecked] = useState(false);
  const [statusValue, setStatusValue] = useState(status);
  const [appIdSel, setAppIdSel] = useState();
  const [clicked, setClicked] = useState();
  const [statusApi, setStatusApi] = useState(true);


  const dispatch = useDispatch();

  const toggleChecked = event => {
    console.log(event.target.value)
    setClicked(event.target.value);
    setChecked(prev => !prev);
    setStatusValue(statusValue === "Active" ? "Inactive" : "Active");
    if (type === "application") {
      dispatch(
        actions.statusUpdateApplication({
          tab: type,
          status: status,
          appId: appIdSel,
          refreshData: refreshData,
          tryThis: tryThis
        })
      );
    } else {
      dispatch(
        actions.statusUpdate({
          tab: type,
          status: status,
          appId: appId,
          typeId: typeId,
          refreshData: refreshData,
          tryThis: tryThis

        })
      );
    }

    // window.scrollTo({ top: "0", behavior: "smooth" });
  };
  useEffect(() => {
    const checkedStatus = status === "Active" ? true : false;
    setChecked(checkedStatus);
    setStatusValue(status);
    setAppIdSel(typeId);
    setStatusApi(statusChanged)
  // }, [status,statusChanged]);
}, [status]);


  const tryThis = (val) => {
    if(val == typeId){
      setChecked(true);
      setStatusValue("Active");
    }
  }
    // useEffect(() => {
    //   console.log("useEffect");
    //   console.log(clicked)
    //   console.log(typeId)
    //   if(statusApi === false){
    //     console.log("enter iside")
    //   }
    //   if(clicked == typeId && statusApi === false){
    //     console.log("entered")
    //     setChecked(true);
    //     setStatusValue("Active");
    //   }
  
    // }, [statusApi,clicked]);
    return (
    <FormGroup>
      <FormControlLabel
        control={
          <Switch
            id={`menu-${typeId}`}
            color="secondary"
            size="small"
            checked={checked}
            onChange={toggleChecked}
            value={typeId}
          />
        }
        label={statusValue}
        labelPlacement={labelPosition}
      />
    </FormGroup>
  );
};

export default SwitchesSize;
